<template>
	<div id="deliveryer-commision">
		<public-header title="提成及提现"></public-header>
		<div class="content">
			<van-radio-group v-model="deliveryer.fee_delivery.takeout.deliveryer_fee_type" class="margin-10-b">
				<van-cell-group>
					<van-cell title="外卖单提成方式">
						<div slot="right-icon" class="c-gray" @click="onTogglePopup('takeout')">
							具体设置<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
					<van-cell clickable @click="deliveryer.fee_delivery.takeout.deliveryer_fee_type = 1; onSetDistanceData('takeout', 'string')" class="border-0px">
						<div slot="title">
							固定金额
							<div class="diy-van-label" v-if="deliveryer.fee_delivery.takeout.deliveryer_fee_type == 1">固定金额{{deliveryer.fee_delivery.takeout.deliveryer_fee}}元</div>
						</div>
						<van-radio :name="1" />
					</van-cell>
					<van-cell clickable @click="deliveryer.fee_delivery.takeout.deliveryer_fee_type = 2; onSetDistanceData('takeout', 'string')" class="border-0px">
						<div slot="title">
							固定比例
							<div class="diy-van-label" v-if="deliveryer.fee_delivery.takeout.deliveryer_fee_type == 2">固定比例{{deliveryer.fee_delivery.takeout.deliveryer_fee}}%</div>
						</div>
						<van-radio :name="2" />
					</van-cell>
					<van-cell clickable @click="deliveryer.fee_delivery.takeout.deliveryer_fee_type = 3; onSetDistanceData('takeout', 'object')" class="border-0px no-fix">
						<div slot="title">
							按距离收取
							<div class="diy-van-label" v-if="deliveryer.fee_delivery.takeout.deliveryer_fee_type == 3">
								每单基础配送费{{deliveryer.fee_delivery.takeout.deliveryer_fee.start_fee}}元,超出{{deliveryer.fee_delivery.takeout.deliveryer_fee.start_km}}km每km增加{{deliveryer.fee_delivery.takeout.deliveryer_fee.pre_km}}元,最高{{deliveryer.fee_delivery.takeout.deliveryer_fee.max_fee}}元
							</div>
						</div>
						<van-radio :name="3" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<van-radio-group v-model="deliveryer.fee_delivery.errander.deliveryer_fee_type" class="margin-10-b">
				<van-cell-group>
					<van-cell title="跑腿单提成方式">
						<div slot="right-icon" class="c-gray" @click="onTogglePopup('errander')">
							具体设置<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
					<van-cell clickable @click="deliveryer.fee_delivery.errander.deliveryer_fee_type = 1; onSetDistanceData('errander', 'string')" class="border-0px">
						<div slot="title">
							固定金额
							<div class="diy-van-label" v-if="deliveryer.fee_delivery.errander.deliveryer_fee_type == 1">固定金额{{deliveryer.fee_delivery.errander.deliveryer_fee}}元</div>
						</div>
						<van-radio :name="1" />
					</van-cell>
					<van-cell clickable @click="deliveryer.fee_delivery.errander.deliveryer_fee_type = 2; onSetDistanceData('errander', 'string')" class="border-0px">
						<div slot="title">
							固定比例
							<div class="diy-van-label" v-if="deliveryer.fee_delivery.errander.deliveryer_fee_type == 2">固定比例{{deliveryer.fee_delivery.errander.deliveryer_fee}}%</div>
						</div>
						<van-radio :name="2" />
					</van-cell>
					<van-cell clickable @click="deliveryer.fee_delivery.errander.deliveryer_fee_type = 3; onSetDistanceData('errander', 'object')" class="border-0px no-fix">
						<div slot="title">
							按距离收取
							<div class="diy-van-label" v-if="deliveryer.fee_delivery.errander.deliveryer_fee_type == 3">
								每单基础配送费{{deliveryer.fee_delivery.errander.deliveryer_fee.start_fee}}元,超出{{deliveryer.fee_delivery.errander.deliveryer_fee.start_km}}km每km增加{{deliveryer.fee_delivery.errander.deliveryer_fee.pre_km}}元,最高{{deliveryer.fee_delivery.errander.deliveryer_fee.max_fee}}元
							</div>
						</div>
						<van-radio :name="3" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<van-cell-group class="margin-10-b">
				<van-cell>
					<div slot="title">最低提现金额</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model="deliveryer.fee_getcash.get_cash_fee_limit" type="text"><span>元</span>
						</div>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">提现费率</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model="deliveryer.fee_getcash.get_cash_fee_rate" type="text" value="5"><span>%</span>
						</div>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">提现费用</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model="deliveryer.fee_getcash.get_cash_fee_min" type="text" value="10"><span>元</span>
						</div>
						<span class="c-gray">~</span>
						<div class="input-unit">
							<input v-model="deliveryer.fee_getcash.get_cash_fee_max" type="text" value="200"><span>元</span>
						</div>
					</div>
				</van-cell>
			</van-cell-group>
			<van-radio-group v-model="deliveryer.fee_getcash.get_cash_period">
				<van-cell-group>
					<van-cell title="提现到账周期"></van-cell>
					<van-cell title="管理员审核" clickable @click="deliveryer.fee_getcash.get_cash_period = 0" class="border-0px">
						<van-radio :name="0" />
					</van-cell>
					<van-cell title="急速到账" clickable @click="deliveryer.fee_getcash.get_cash_period = 1" class="border-0px">
						<van-radio :name="1" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<div class="padding-15">
				<van-button size="normal" type="primary" block :disabled="!islegal" @click="onSubmit" class="font-16">提交</van-button>
			</div>
		</div>
		<!--外卖具体设置弹出层-->
		<van-popup v-model="popupStatus.takeout" position="bottom">
			<div class="popup-title">
				<span class="c-gray" @click="onTogglePopup('takeout')">取消</span>
				<span v-if="deliveryer.fee_delivery.takeout.deliveryer_fee_type == 1">按固定金额收取</span>
				<span v-else-if="deliveryer.fee_delivery.takeout.deliveryer_fee_type == 2">按固定比例收取</span>
				<span v-else-if="deliveryer.fee_delivery.takeout.deliveryer_fee_type == 3">按距离收取</span>
				<span class="c-primary" @click="onTogglePopup('takeout')">确定</span>
			</div>
			<div class="popup-content">
				<van-cell-group v-if="deliveryer.fee_delivery.takeout.deliveryer_fee_type == 1">
					<van-cell>
						<div slot="title">每单固定</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input v-model="deliveryer.fee_delivery.takeout.deliveryer_fee" type="text"><span>元</span>
							</div>
						</div>
					</van-cell>
				</van-cell-group>
				<van-cell-group v-else-if="deliveryer.fee_delivery.takeout.deliveryer_fee_type == 2">
					<van-cell>
						<div slot="title">每单按照订单配送费提成</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input v-model="deliveryer.fee_delivery.takeout.deliveryer_fee" type="text"><span>%</span>
							</div>
						</div>
					</van-cell>
				</van-cell-group>
				<van-cell-group v-else-if="deliveryer.fee_delivery.takeout.deliveryer_fee_type == 3">
					<van-cell>
						<div slot="title">每单基础配送费</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input type="text" v-model="deliveryer.fee_delivery.takeout.deliveryer_fee.start_fee"><span>元</span>
							</div>
						</div>
					</van-cell>
					<van-cell>
						<div slot="title">超出</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input type="text" v-model="deliveryer.fee_delivery.takeout.deliveryer_fee.start_km"><span>KM</span>
							</div>
						</div>
					</van-cell>
					<van-cell>
						<div slot="title">每增加1KM加收</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input type="text" v-model="deliveryer.fee_delivery.takeout.deliveryer_fee.pre_km"><span>元</span>
							</div>
						</div>
					</van-cell>
					<van-cell>
						<div slot="title">最高收取</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input type="text" v-model="deliveryer.fee_delivery.takeout.deliveryer_fee.max_fee"><span>元</span>
							</div>
						</div>
					</van-cell>
				</van-cell-group>
			</div>
		</van-popup>
		<!--跑腿具体设置弹出层-->
		<van-popup v-model="popupStatus.errander" position="bottom">
			<div class="popup-title">
				<span class="c-gray" @click="onTogglePopup('errander')">取消</span>
				<span v-if="deliveryer.fee_delivery.errander.deliveryer_fee_type == 1">按固定金额收取</span>
				<span v-else-if="deliveryer.fee_delivery.errander.deliveryer_fee_type == 2">按固定比例收取</span>
				<span v-else-if="deliveryer.fee_delivery.errander.deliveryer_fee_type == 3">按距离收取</span>
				<span class="c-primary" @click="onTogglePopup('errander')">确定</span>
			</div>
			<div class="popup-content">
				<van-cell-group v-if="deliveryer.fee_delivery.errander.deliveryer_fee_type == 1">
					<van-cell>
						<div slot="title">每单固定</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input v-model="deliveryer.fee_delivery.errander.deliveryer_fee" type="text"><span>元</span>
							</div>
						</div>
					</van-cell>
				</van-cell-group>
				<van-cell-group v-else-if="deliveryer.fee_delivery.errander.deliveryer_fee_type == 2">
					<van-cell>
						<div slot="title">每单按照订单配送费提成</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input v-model="deliveryer.fee_delivery.errander.deliveryer_fee" type="text"><span>%</span>
							</div>
						</div>
					</van-cell>
				</van-cell-group>
				<van-cell-group v-else-if="deliveryer.fee_delivery.errander.deliveryer_fee_type == 3">
					<van-cell>
						<div slot="title">每单基础配送费</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input type="text" v-model="deliveryer.fee_delivery.errander.deliveryer_fee.start_fee"><span>元</span>
							</div>
						</div>
					</van-cell>
					<van-cell>
						<div slot="title">超出</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input type="text" v-model="deliveryer.fee_delivery.errander.deliveryer_fee.start_km"><span>KM</span>
							</div>
						</div>
					</van-cell>
					<van-cell>
						<div slot="title">每增加1KM加收</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input type="text" v-model="deliveryer.fee_delivery.errander.deliveryer_fee.pre_km"><span>元</span>
							</div>
						</div>
					</van-cell>
					<van-cell>
						<div slot="title">最高收取</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input type="text" v-model="deliveryer.fee_delivery.errander.deliveryer_fee.max_fee"><span>元</span>
							</div>
						</div>
					</van-cell>
				</van-cell-group>
			</div>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	export default {
		data() {
			return {
				id: 0,
				deliveryer: {
					fee_delivery: {
						takeout: {
							deliveryer_fee_type: 3,
							deliveryer_fee: {
								start_fee: 0,
								start_km: 0,
								pre_km: 0,
								max_fee: 0
							}
						},
						errander: {
							deliveryer_fee_type: 3,
							deliveryer_fee: {
								start_fee: 0,
								start_km: 0,
								pre_km: 0,
								max_fee: 0
							}
						}
					},
					fee_getcash: {
						get_cash_fee_limit: '',
						get_cash_fee_max: '',
						get_cash_fee_min: '',
						get_cash_fee_rate: '',
						get_cash_period: ''
					}
				},
				popupStatus: {
					takeout: false,
					errander: false
				},
				deliveryerFeeOld: {
					takeout: {
						string: 0,
						object: {
							start_fee: 0,
							start_km: 0,
							pre_km: 0,
							max_fee: 0
						}
					},
					errander: {
						string: 0,
						object: {
							start_fee: 0,
							start_km: 0,
							pre_km: 0,
							max_fee: 0
						}
					},

				},
				showPreLoading: true,
				islegal: false
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onTogglePopup(type) {
				this.popupStatus[type] = !this.popupStatus[type];
			},
			onSetDistanceData(type, objectOrString) {
				this.deliveryer.fee_delivery[type].deliveryer_fee = this.deliveryerFeeOld[type][objectOrString];
			},
			onLoad() {
				if(!this.id) {
					this.util.$toast('配送员不存在');
					return false;
				}
				if(!this.id) {
					this.islegal = true;
					return false;
				}
				this.util.request({
					url: 'plateform/deliveryer/plateform/comision',
					data: {
						id: this.id
					}
				}).then((res) => {
					this.showPreLoading = false;
					var result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message);
						return false;
					}
					result = result.message;
					if(result.deliveryer) {
						delete result.deliveryer.password;
						if(result.deliveryer.fee_delivery && result.deliveryer.fee_delivery.takeout && (result.deliveryer.fee_delivery.takeout.deliveryer_fee_type == 1 || result.deliveryer.fee_delivery.takeout.deliveryer_fee_type == 2)) {
							this.deliveryer.fee_delivery.takeout.deliveryer_fee = '';
						}
						if(result.deliveryer.fee_delivery && result.deliveryer.fee_delivery.errander && (result.deliveryer.fee_delivery.errander.deliveryer_fee_type == 1 || result.deliveryer.fee_delivery.errander.deliveryer_fee_type == 2)) {
							this.deliveryer.fee_delivery.errander.deliveryer_fee = '';
						}
						this.deliveryer = this.util.extend(this.deliveryer, result.deliveryer);
						this.onSetDeliveryerFeeOld('takeout', this.deliveryer.fee_delivery.takeout.deliveryer_fee_type);
						this.onSetDeliveryerFeeOld('errander', this.deliveryer.fee_delivery.errander.deliveryer_fee_type);
					}
					this.islegal = true;
				})

			},
			onSubmit() {
				if(!this.islegal) {
					return false;
				}
				this.islegal = false;
				this.util.request({
					url: 'plateform/deliveryer/plateform/comision',
					method: 'POST',
					data: {
						id: this.id,
						deliveryer: this.deliveryer,
					}
				}).then((res) => {
					var result = res.data.message;
					this.islegal = true;
					if(result.errno) {
						this.util.$toast(result.message);
						return false;
					}
					this.util.$toast(result.message, this.util.getUrl({path: 'pages/deliveryer/guide', query: {id: this.id}}), 1500, 'replace');
				})
			},
			onSetDeliveryerFeeOld(type, mode) {
				let key = 'string';
				if(mode == 3) {
					key = 'object';
				}
				this.deliveryerFeeOld[type][key] = this.deliveryer.fee_delivery[type].deliveryer_fee;
			}

		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = this.$route.query.id;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#deliveryer-commision .van-icon-arrow{
		color: #999;
		font-size: 12px;
		line-height: 24px;
		margin-left: 5px;
	}

</style>


